import Vue from 'vue'
import Component from 'vue-class-component'

@Component
export class TagHelper extends Vue{
  createTag(){
    const name = window.prompt('');
    if (!name) {return window.alert('');}
    this.$store.commit('createTag', name);
    if(this.$store.state.createTagStatus){
      window.alert('');
    }
  }
}